/* General Layout */
body {
  font-family: "Comic Sans MS", sans-serif;
  background: linear-gradient(130deg, #ffd86f, #ffb4e6, #9fd5ff);
  background-size: 200% 200%;
  animation: bgShift 10s alternate infinite;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  margin: 0;
}

@keyframes bgShift {
  0% { background-position: left top; }
  100% { background-position: right bottom; }
}

/* Game Container */
.game-container {
  width: 92vw;
  max-width: 650px;
  padding: 24px;
  background: rgba(255, 255, 255, 0.88);
  border-radius: 22px;
  text-align: center;
  box-shadow: 0 0 35px rgba(0, 0, 0, 0.25);
  border: 4px solid #f2d35c;
}

/* Title + Score */
.title {
  font-size: 2.3rem;
  margin-top: 0;
  text-shadow: 1px 2px rgba(0, 0, 0, 0.2);
}

.score {
  font-size: 1.3rem;
  margin-bottom: 10px;
}

/* Character Sprite */
.character {
  width: 45%;
  max-width: 280px;
  margin-bottom: 8px;
  image-rendering: crisp-edges;
  background: transparent;
}

/* Definition text */
.definition {
  font-size: 1.1rem;
  font-style: italic;
  margin-bottom: 15px;
}

/* Masked Word Row */
.masked-word {
  display: flex;
  justify-content: center;
  gap: 8px;
  margin: 10px 0 18px;
}

.masked-letter {
  width: 42px;
  height: 54px;
  font-size: 2rem;
  font-weight: bold;
  border-radius: 8px;
  border: 3px solid #6a5436;
  background: #f5e3c2;
  color: #3e2a1b;
  box-shadow: 0 4px #c2a273;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

.masked-letter.blank {
  background: #efe1c9;
}

.masked-letter:active {
  transform: translateY(2px);
  box-shadow: 0 1px #c2a273;
}

/* Letter Bank */
.letter-bank {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 12px;
  margin-top: 6px;
}

/* Letter Tiles (stone style) */
.letter-tile {
  background: #d4c0a1;
  color: #3e2a1b;
  width: 58px;
  height: 58px;
  font-size: 1.8rem;
  font-weight: bold;
  border-radius: 6px;
  border: 3px solid #6a5436;
  cursor: pointer;
  box-shadow: 0px 4px #897454;
  transition: 0.12s;
}

.letter-tile:active {
  transform: translateY(3px);
  box-shadow: none;
}

/* Feedback + Retry */
.feedback {
  font-size: 1.4rem;
  font-weight: bold;
  margin-top: 15px;
  min-height: 30px;
}

.retry-button {
  margin-top: 10px;
  padding: 8px 16px;
  font-size: 1rem;
  border-radius: 12px;
  border: none;
  background: #ffb347;
  color: #3e2a1b;
  font-weight: bold;
  cursor: pointer;
  box-shadow: 0 4px #d28a2d;
}

.retry-button:active {
  transform: translateY(2px);
  box-shadow: 0 1px #d28a2d;
}

/* Mobile tweaks */
@media (max-width: 480px) {
  .title {
    font-size: 2rem;
  }

  .masked-letter {
    width: 36px;
    height: 48px;
    font-size: 1.7rem;
  }

  .letter-tile {
    width: 48px;
    height: 48px;
    font-size: 1.5rem;
  }

  .character {
    width: 55%;
  }
}
